  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.card__product-code {
  color: #616070;
  text-align: left;
  width: 100%;
  margin-bottom: 16px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__rate {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__rate-number-review {
  color: #060b35;
  text-align: right;
  height: 100%;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  flex-direction: row;
  justify-content: space-between;
  align-items: end;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  text-align: left;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
